.inavii-social-feed-app {
  &__done {
    padding-left: 0;
    padding-right: 0;

    .anticon {
      color: #7a3cff !important;
    }

    &-video {
      width: 100%;
      border-radius: 8px;
    }

    &-container {
      .ant-image {
        width: 100%;
        border: 1px dashed rgb(217, 217, 217);
        border-radius: 8px;
        overflow: hidden;
      }
    }
  }

  &__skeleton {
    position: absolute;
    top: 0;
    left: 0;
    height: 100% !important;
    width: 100% !important;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: white;
    }
  }
}