.inavii-social-feed-app {
  &__account-info-box {
    width: 320px;
    color: rgba(0, 0, 0, 0.6);

    .ant-card-body {
      padding-top: 8px;
      text-align: left;
    }
  }

  &__account-info-button-box {
    display: flex;
    flex-direction: column;
    justify-content: center;

    button {
      &:first-of-type {
        margin-top: 0;
      }
    }
  }

  &__account-info-avatar {
    display: flex;
    align-items: center;
    gap: 20px;

    &-img {
      height: 70px;
      width: 70px;
      min-width: 70px;
      background-color: transparent;
      border: none;
    }
  }

  &__account-info-button {
    width: max-content;
  }

  &__account-info-button--delete {
    margin-top: 16px;
    color: #7a3cff;
    cursor: pointer;
    transition: color .3s;

    &:hover,
    &:focus {
      color: #ac8dfc;
    }
  }

  &__account-info-title {
    font-weight: bold;
    margin-right: 6px;
    display: inline-flex;
    color: #000000;
  }

  &__account-info-type {
    text-transform: uppercase;

    span {
      text-transform: capitalize;
    }
  }

  &__account-info-avatar,
  &__account-info-username,
  &__account-info-nickname,
  &__account-info-user-id {
    padding-bottom: 10px;
  }
}